import { ArcRotateCamera, Camera } from "@babylonjs/core";
import { Engine } from "@babylonjs/core/Engines/engine";
import { Scene } from "@babylonjs/core/scene";
import { Vector3, Viewport } from "@babylonjs/core/Maths/math";
import { MaterialManager } from "./MaterialManager";
import { AppManager } from "./AppManager";


export class SceneManager {
    private appManager: AppManager

    constructor(appManager: AppManager) {
        this.appManager = appManager;

        this.setScene();
        this.setCamera();
        this.setEnvironment();


    }
    private canvas: HTMLCanvasElement;
    private engine: Engine;
    private scene: Scene;
    private camera: ArcRotateCamera;

    public getCanvas(): HTMLCanvasElement {
        return this.canvas;
    }
    private setScene() {
        this.canvas = document.getElementById("renderCanvas") as HTMLCanvasElement;
        this.engine = new Engine(this.canvas);
        this.scene = new Scene(this.engine);

        
    // scene.onPointerDown = function() {
    //     var ray = scene.createPickingRay(scene.pointerX, scene.pointerY, BABYLON.Matrix.Identity(), camera);	
    //     var hit = scene.pickWithRay(ray);
    //     if (hit.pickedMesh && hit.pickedMesh.name === 'sphere') {
    //         console.log('clicked');
    //     }
    // }   

    // scene.onPointerMove = function() {
    //     var ray = scene.createPickingRay(scene.pointerX, scene.pointerY, BABYLON.Matrix.Identity(), camera);	
    //     var hit = scene.pickWithRay(ray);
    //     if (hit.pickedMesh && hit.pickedMesh.name === 'sphere') {
    //         scene.defaultCursor = 'pointer';
    //     } else {
    //         scene.defaultCursor = "default";
    //     }
    // } 
    }
    public getScene(): Scene {
        return this.scene;
    }

    public getEngine(): Engine {
        return this.engine;
    }
    private setCamera() {
        this.camera = new ArcRotateCamera("camera1", 0, 0, 0, new Vector3(0, 0, 0), this.scene);
        this.camera.setPosition(new Vector3(0, 0, 0.1));
        this.camera.attachControl(this.canvas, true);
        this.camera.minZ = 0;
        this.camera.inertia = 0.7;
        this.camera.lowerRadiusLimit = this.camera.upperRadiusLimit = this.camera.radius = 1;
        this.camera.lowerBetaLimit = this.camera.upperBetaLimit = this.camera.beta = 1.56;


    }
    public getCamera(): ArcRotateCamera {
        return this.camera;
    }
    private setEnvironment() {

        // this.camera.viewport = new Viewport(0, 0, 1, 1);
        this.scene.activeCamera = this.camera;
        this.scene.createDefaultLight();

        this.engine.runRenderLoop(() => {
            this.scene.render();
            // console.log(">>")
        });

        window.addEventListener('resize', () => {
            // console.log("windowresize");
            this.scene.render();

        }, false);

        this.camera.onViewMatrixChangedObservable.add(() => {
            if (this.appManager.wallGeometry)
                this.appManager.wallGeometry.update();
        });
    }


}