        //     //     // const pick = scene.pick(scene.pointerX, scene.pointerY);
        //     //     gizmoManager.attachToMesh(data.center);
        //     //     gizmoManager.positionGizmoEnabled = true;
        //     //     gizmoManager.rotationGizmoEnabled = true;
        //     //     gizmoManager.scaleGizmoEnabled = true;
        //     //     //updateCubePoints(cubeCenterPointsBoxes[index]);
        //     // }
    
        // }));
        // this.sphere.actionManager.registerAction(new ExecuteCodeAction({ trigger: ActionManager.OnPickDownTrigger }, // 'E' key
        //  ()=> {
