     changeRoomMaterial(material: Material) {
        this.sphere.material = material;
    }

    createRoomMaterial(url: string) {
        /******Apply texture to custom mesh based on uv coordinates.****************/
        // var material = new BABYLON.StandardMaterial("RoomName_Mat", scene);
        // material.diffuseTexture = new BABYLON.Texture(folderPath + room_Images[roomNo], scene);
        // material.alpha = 1;
        // material.backFaceCulling = false;
        // material.specularColor = new BABYLON.Color3(0, 0, 0);


        var pbr:any = new PBRMetallicRoughnessMaterial("pbr", this.scene);
        // pbr.baseColor = new BABYLON.Color3(1.0, 0.766, 0.336);
        pbr.metallic = 0; // set to 1 to only use it from the metallicRoughnessTexture
        pbr.roughness = 1.0; // set to 1 to only use it from the metallicRoughnessTexture
        // pbr.environmentTexture = BABYLON.CubeTexture.CreateFromPrefilteredData("/textures/environment.dds", scene);
        // pbr.metallicRoughnessTexture = new BABYLON.Texture(folderPath + room_Images[roomNo], scene);
        console.log(pbr);
        pbr._albedoTexture = new Texture(url, this.scene);

        // // pbr.disableLighting = true
        pbr.backFaceCulling = false;
        return pbr;
    }

    toggleSphere(force?:any){
        // console.log(this.sphere.isVisible)
        this.sphere.isVisible= !this.sphere.isVisible;
        if(force)
        this.sphere.isVisible= force;
       
    }
