
import { DH_Constants } from "../Room/DH_Constants";
import { AppManager } from "./AppManager";

export class ModeManager {
    private appManager: AppManager;



    constructor(appManager: AppManager) {
        this.appManager = appManager;

    }

    // isWallModeEnabled = false;
    toggleWallMode() {

        if (DH_Constants.modeCube) {
            this.toggleCubeMode();
            this.appManager.cubeGeometry.disableAllCube();
        }
        // Constants.disableAllMode();
        DH_Constants.modePlane = !DH_Constants.modePlane;
        console.log("Wall Mode :", DH_Constants.modePlane)
        if (DH_Constants.modePlane && !DH_Constants.modePlane_createCorners && !DH_Constants.modePlane_editWallUV)
            this.appManager.DHCursor.updateMsg("Select Options from below");

        this.appManager.wallGeometry.enableWallOptionsUi();

        if (DH_Constants.modePlane)
            this.appManager.wallGeometry.enableAll();
        else
            this.appManager.wallGeometry.disableAll();

        this.appManager.cubeGeometry.disableAllCube();



    }

    toggleCubeMode() {

        if (DH_Constants.modePlane)
            this.toggleWallMode()

        DH_Constants.disableAllWallMode();
        this.appManager.wallGeometry.disableAll();
        
        DH_Constants.modeCube = !DH_Constants.modeCube;
        console.log("Cube Mode :", DH_Constants.modeCube)
        if (DH_Constants.modeCube && !DH_Constants.modeCube_CreateNewCube)
            this.appManager.DHCursor.updateMsg("Select Options from below");

        this.appManager.cubeGeometry.enableDefaultOptionsUI();

        if (DH_Constants.modeCube)
            this.appManager.cubeGeometry.enableAllCube();
        else
            this.appManager.cubeGeometry.disableAllCube();

     


    }

    toggleEditRoomMode() {
        if (DH_Constants.modeCube) {
            this.toggleCubeMode();
        }

        if (DH_Constants.modePlane)
            this.toggleWallMode()

        DH_Constants.modeCube_editRoom = !DH_Constants.modeCube_editRoom;

        if (DH_Constants.modeCube_editRoom) {
            DH_Constants.modeCube_edit_CubeWallUVPosition = false;
            this.appManager.cubeGeometry.enableAllCube();
            this.appManager.cubeGeometry.enableEditRoom();
            this.appManager.wallGeometry.enableAll();
            this.appManager.wallGeometry.enableEditRoom();

        }

    }


}