import { Mesh, Scene } from "@babylonjs/core";
import { ArcRotateCamera } from "@babylonjs/core/Cameras/arcRotateCamera";
import { Vector3 } from "@babylonjs/core/Maths/math.vector";
import { contains } from "jquery";
import { DH_Constants } from "../Room/DH_Constants";
import { WallGeometry } from "../Room/WallGeometry";
import { AppManager } from "./AppManager";
import { Handler } from "./Handler";
import { Linear, TimelineMax, TweenMax } from "gsap";


export class EventManager {
    private appManager: AppManager;
    private camera: ArcRotateCamera;
    private scene: Scene;
    private sphere: Mesh;
    private handler: Handler;
    private wallGeometry: WallGeometry;

    private cursor: any;

    constructor(appManager: AppManager) {


        this.appManager = appManager;
        this.camera = this.appManager.sceneManager.getCamera();
        this.scene = this.appManager.sceneManager.getScene();
        this.sphere = this.appManager.sphereManager.getSphere();
        console.log(this.appManager.handler)
        this.handler = this.appManager.handler.getHandler();
        this.wallGeometry = this.appManager.wallGeometry.getWallGeometryInstance();

        this.appManager.sceneManager.getCanvas().addEventListener("dblclick", () => this.onDoubleClick());
        this.appManager.sceneManager.getCanvas().addEventListener('wheel', (event) => this.onDocumentMouseWheel(event));
        // document.addEventListener('keypress', logKey);
        document.addEventListener("keydown", (event) => this.onKeyDown(event));

        this.mouseControls();

        this.cursor = this.appManager.DHCursor.getCursor();


    }

    onKeyDown(event: any) {

        // console.log(event);
        if (event.key == "2")
            this.appManager.cameraManager.freeCamera();
        if (event.key == "1")
            this.appManager.cameraManager.resetCamera();
        if (event.key == "3")
            this.appManager.cameraManager.setVerticalCamera();
        if (event.key == "4")
            this.appManager.cameraManager.dollhouseCamera();

        if (event.key == "s")
            this.appManager.sphereManager.toggleSphere();
        if (event.key == "c") {
            console.log(this.appManager)
            console.log("DH_Constants.modePlane :", DH_Constants.modePlane)
            console.log("DH_Constants.modePlane_createCorners :", DH_Constants.modePlane_createCorners)
            console.log("DH_Constants.modePlane_editFloorUV :", DH_Constants.modePlane_editFloorUV)
            console.log("DH_Constants.modePlane_editFromTop :", DH_Constants.modePlane_editFromTop)
            console.log("DH_Constants.modePlane_editWallUV :", DH_Constants.modePlane_editWallUV)

            console.log("DH_Constants.modeCube :", DH_Constants.modeCube)
            console.log("DH_Constants.modeCube_CreateNewCube :", DH_Constants.modeCube_CreateNewCube)
            console.log("DH_Constants.modeCube_updatingCubeCenter :", DH_Constants.modeCube_updatingCubeCenter)
            console.log("DH_Constants.modeCube_edit_Cube :", DH_Constants.modeCube_edit_Cube)
            console.log("DH_Constants.modeCube_edit_CubeWallUVPosition :", DH_Constants.modeCube_edit_CubeWallUVPosition)
            console.log(DH_Constants.currentRoomName);
            console.log(DH_Constants.currentWallGroupName);
            // console.log("DH_Constants.modeCube_updatingCubeWalls :",DH_Constants.modeCube_edit_CubeWallCornerPosition)
            if (this.appManager.gizmoHandler.gizmoManager.gizmos.positionGizmo) {
                this.appManager.gizmoHandler.gizmoManager.gizmos.positionGizmo.yGizmo._scaleRatio = 0;
                alert(">>");
            }
            
            // this.appManager.wallGeometry.createFloorFromPoints();
        }
        // console.log(DH_Constants.modePlane)

        // if (DH_Constants.modePlane) {

        console.log(event.key)
        // if (event.key == "w")
        //     this.wallGeometry.enableCornerCreation()

        if (event.key == "e")
            this.wallGeometry.createFloorFromEarcut()

        if (event.key == "f")
            this.appManager.jsonManager.exportTojson()
        // }



    }
    private slectedObj: any;
    mouseControls() {
        this.scene.onPointerDown = (evt, pickResult) => {
            // console.log(pickResult);
            if (pickResult.hit) {
                this.slectedObj = pickResult.pickedMesh;
            }

        };
        this.scene.onPointerMove = (evt, pickResult) => {

            if (this.cursor)
                this.moveCursor(evt);

            var pickinfo: any = this.scene.pick(this.scene.pointerX, this.scene.pointerY, (mesh) => {
                return mesh === this.sphere;
            });
            if (!pickinfo.hit)
                return;

            if (this.wallGeometry && DH_Constants.modePlane) {
                if (DH_Constants.modePlane_createCorners)
                    this.wallGeometry.updatePointer(pickinfo.pickedPoint);
                else
                    this.wallGeometry.disablePointer();

                if (DH_Constants.modePlane_editWallUV && this.slectedObj && this.slectedObj.hasOwnProperty("tag") && this.slectedObj.tag == "CornerBox") {
                    // console.log(this.slectedObj);
                    this.slectedObj.position = pickinfo.pickedPoint;
                    this.wallGeometry.updateWall(this.slectedObj, true, "wall");
                } else if (DH_Constants.modePlane_editFloorUV && this.slectedObj && this.slectedObj.hasOwnProperty("tag") && this.slectedObj.tag == "CornerBox") {
                    // console.log(this.slectedObj);
                    this.slectedObj.position = pickinfo.pickedPoint;
                    this.wallGeometry.updateWall(this.slectedObj, true, "floor");
                }
            }
            else
                this.wallGeometry.disablePointer();


        };
        this.scene.onPointerUp = (evt, pickResult) => {
            if (this.slectedObj) {

                console.log(this.wallGeometry);
                console.log(DH_Constants.modePlane);
                console.log(DH_Constants.modePlane_editWallUV);
                console.log(this.slectedObj);

                if (this.wallGeometry && DH_Constants.modePlane) {


                    if (DH_Constants.modePlane_editWallUV && this.slectedObj && this.slectedObj.hasOwnProperty("tag") && this.slectedObj.tag == "CornerBox") {

                        //   this.wallGeometry.updateAllWallUv(this.slectedObj);

                    }
                }
                this.slectedObj = null;
            }
        };
    }
    onDoubleClick() {


        var pickinfo: any = this.scene.pick(this.scene.pointerX, this.scene.pointerY, (mesh: any) => {
            //  if(mesh.isPickable)
            return mesh;
            // else 
            //  return null ;
        });
        if (pickinfo.pickedMesh != null && pickinfo && pickinfo.hit) {

            console.log(pickinfo.pickedMesh);
            console.log("DH_Constants.modePlane ", DH_Constants.modePlane);
            console.log("DH_Constants.modePlane_createCorners ", DH_Constants.modePlane_createCorners);
            // console.log(pickinfo.pickedMesh);

            if (DH_Constants.modePlane && DH_Constants.modePlane_createCorners)
                this.wallGeometry.addFloorPoint(pickinfo.pickedPoint);
            else if (DH_Constants.modeCube && DH_Constants.modeCube_CreateNewCube)
                this.appManager.cubeGeometry.createCube(pickinfo.pickedPoint);


        }
        if (pickinfo.pickedMesh != null) {

            // if (pickinfo.pickedMesh != null && pickinfo.pickedMesh.tag == "wall" && !createWall) {

            // RoomData[CurrentRoomName].wall


            // selectedBox.position=pickinfo.pickedPoint;
            // updateUV(selectedBox.name,pickinfo.getTextureCoordinates().x,pickinfo.getTextureCoordinates().y);
            // path[0] = box0.position;
            // path[1] = box1.position;
            // path[2] = box2.position;
            // path[3] = box3.position;
            // path[4] = box0.position;
            // displayLine = BABYLON.MeshBuilder.CreateLines("", {points: path, instance: displayLine})
            // let box0 = new BABYLON.MeshBuilder.CreateBox("point0",{size:15},this.scene);
        }


        // if (refmousePointer != undefined && wallMousePointer)
        //     refmousePointer.scaling.set(1, 1, 1);
    }
    onDocumentMouseWheel(event: any) {

        // if(updating)
        // return;
        if (DH_Constants.updatefov) {
            var fovMAX = 2.8;
            var fovMIN = 0.5;

            this.camera.fov -= event.wheelDeltaY / 2 * 0.005;
            this.camera.fov = Math.max(Math.min(this.camera.fov, fovMAX), fovMIN);
        }
        else
            this.camera.fov = 0.8;
    }

    moveCursor(e: any) {

        TweenMax.to(this.cursor[0], 0.3, {
            left: e.clientX,
            top: e.clientY
        });
    }
}