import { AppManager } from "./AppManager";

export class DH_Cursor{

    private cursor:any;

    constructor(appManager: AppManager) {
        this.cursor = document.getElementsByClassName("cursorBox");
    }

    public getCursor(): DH_Cursor {
        return this.cursor;
    }
    public updateMsg(msg:string){
        this.cursor[0].textContent = msg;
    }

}