import { ArcRotateCamera } from "@babylonjs/core/Cameras/arcRotateCamera";
import { Vector3 } from "@babylonjs/core/Maths/math.vector";
import { DH_Constants } from "../Room/DH_Constants";
import { AppManager } from "../ts/AppManager";
// import * as $ from "jquery";


export class CameraManager {


    private appManager: AppManager;
    private camera: ArcRotateCamera;
    constructor(appManager: AppManager) {
        this.appManager = appManager;
        this.camera = this.appManager.sceneManager.getCamera();
        this.resetCamera();
    }

    addCameraListener() {

    }

    setVerticalCamera() {
        this.camera.lowerBetaLimit = this.camera.upperBetaLimit = this.camera.beta = 0.01;
        this.camera.panningSensibility = 100;
        this.camera.upperRadiusLimit = 20000;
        this.camera.maxZ = 30000;
        // this.camera.panningInertia=0.8;
        this.camera.wheelDeltaPercentage = 0.1;
    }
    resetCamera() {
        this.camera.setTarget(Vector3.Zero());
        this.camera.setPosition(new Vector3(0, 0, 0.1));
        this.camera.minZ = 0;
        this.camera.panningSensibility = 0; // Panning
        //  updatefov = false;
        this.camera.upperRadiusLimit = 20000;
        this.camera.maxZ = 30000;
        this.camera.lowerRadiusLimit = -100;
        this.camera.lowerBetaLimit = 0.01;
        this.camera.upperBetaLimit = 3.13;
        this.camera.fov = 0.8;
        this.camera.panningSensibility = 0;
        this.camera.inertia = 0.7;
        this.camera.lowerRadiusLimit = this.camera.upperRadiusLimit = this.camera.radius = 1;
        this.camera.lowerBetaLimit = this.camera.upperBetaLimit = this.camera.beta = 1.56;
        DH_Constants.updatefov = true;
    }

    freeCamera() {
         DH_Constants.updatefov = false;
        this.camera.upperRadiusLimit = 20000;
        this.camera.maxZ = 30000;
        this.camera.lowerRadiusLimit = -100
        this.camera.lowerBetaLimit = 0.01;
        this.camera.upperBetaLimit = 3.13;
        // sphere.visibility=0;
        this.camera.fov = 0.8;
        this.camera.panningSensibility = 100;
    }

    dollhouseCamera(){
        DH_Constants.updatefov = false;

        this.camera.lowerRadiusLimit = -100
    
        // sphere.isVisible = false;
        this.camera.fov = 0.8;
        this.camera.panningSensibility = 100;
    
        this.camera.lowerBetaLimit = 0.01;
        this.camera.upperBetaLimit = 3.13;
    
        this.camera.panningSensibility = 100;
        this.camera.upperRadiusLimit = 20000;
        this.camera.maxZ = 30000;
        // camera.panningInertia=0.8;
        this.camera.wheelDeltaPercentage = 0.1;
    }




}