import { float, Scene } from "@babylonjs/core";
import { GizmoManager, PositionGizmo } from "@babylonjs/core/Gizmos";
import { DH_Constants } from "../Room/DH_Constants";
import { AppManager } from "./AppManager";

export class GizmoHandler {
    private appManager: AppManager;
    private scene: Scene;
    gizmoManager: GizmoManager;

    mesh: any;

    constructor(appManager: AppManager) {
        this.appManager = appManager;
        this.scene = appManager.sceneManager.getScene();
        this.gizmoManager = new GizmoManager(this.scene);
        this.gizmoManager.positionGizmoEnabled = true;
        this.gizmoManager.rotationGizmoEnabled = true;
        this.gizmoManager.scaleGizmoEnabled = true;
        // this.gizmoManager.po
        // this.gizmoManager.camera
        // this.gizmoManager = new PositionGizmo(this.utilLayer);
        // this.gizmoManager.utilLayer = this.utilLayer;

        // this.gizmoManager.rotationGizmoEnabled = false;
        // this.gizmoManager.scaleGizmoEnabled = true;
        // this.gizmoManager.boundingBoxGizmoEnabled = false;
        this.gizmoManager.usePointerToAttachGizmos = false;
        this.gizmoManager.gizmos.positionGizmo.xGizmo.dragBehavior.onDragObservable.add((event) => {
          
            this.onMeshDraging(event, "x")

        });
        this.gizmoManager.gizmos.positionGizmo.yGizmo.dragBehavior.onDragObservable.add((event) => {
            this.onMeshDraging(event, "y")

        })
        this.gizmoManager.gizmos.positionGizmo.zGizmo.dragBehavior.onDragObservable.add((event) => {
            this.onMeshDraging(event, "z")
        });


        // this.gizmoManager.gizmos.positionGizmo?.xGizmo
        this.gizmoManager.gizmos.positionGizmo?.xGizmo.dragBehavior.onDragEndObservable.add((event: any) => {
            console.log("Position gizmo's x axis drag was ended", event);
            this.onMeshTransformUpdate("position")
        })
        // this.gizmoManager.gizmos.positionGizmo.yGizmo.dragBehavior.onDragStartObservable.add((event: any) => {
        //     console.log("Position gizmo's y axis started to be dragged");
        // })
        this.gizmoManager.gizmos.positionGizmo?.yGizmo.dragBehavior.onDragEndObservable.add((event: any) => {
            //  console.log("Position gizmo's y axis drag was ended", event);
            this.onMeshTransformUpdate("position")
        })
        // this.gizmoManager.gizmos.positionGizmo.zGizmo.dragBehavior.onDragStartObservable.add((event: any) => {
        //     console.log("Position gizmo's z axis started to be dragged");
        // })
        this.gizmoManager.gizmos.positionGizmo?.zGizmo.dragBehavior.onDragEndObservable.add((event: any) => {
            //    console.log("Position gizmo's z axis drag was ended", event);
            this.onMeshTransformUpdate("position")
        });

        this.gizmoManager.gizmos.rotationGizmo?.xGizmo.dragBehavior.onDragEndObservable.add((event: any) => {
            console.log("Position gizmo's x axis drag was ended", event);
            this.onMeshTransformUpdate("rotation")
        })
        // this.gizmoManager.gizmos.positionGizmo.yGizmo.dragBehavior.onDragStartObservable.add((event: any) => {
        //     console.log("Position gizmo's y axis started to be dragged");
        // })
        this.gizmoManager.gizmos.rotationGizmo?.yGizmo.dragBehavior.onDragEndObservable.add((event: any) => {
            //  console.log("Position gizmo's y axis drag was ended", event);
            this.onMeshTransformUpdate("rotation")
        })
        // this.gizmoManager.gizmos.positionGizmo.zGizmo.dragBehavior.onDragStartObservable.add((event: any) => {
        //     console.log("Position gizmo's z axis started to be dragged");
        // })
        this.gizmoManager.gizmos.rotationGizmo?.zGizmo.dragBehavior.onDragEndObservable.add((event: any) => {
            //    console.log("Position gizmo's z axis drag was ended", event);
            this.onMeshTransformUpdate("rotation")
        })

        this.gizmoManager.gizmos.scaleGizmo?.xGizmo.dragBehavior.onDragEndObservable.add((event: any) => {
            console.log("Position gizmo's x axis drag was ended", event);
            this.onMeshTransformUpdate("scale")
        })
        // this.gizmoManager.gizmos.positionGizmo.yGizmo.dragBehavior.onDragStartObservable.add((event: any) => {
        //     console.log("Position gizmo's y axis started to be dragged");
        // })
        this.gizmoManager.gizmos.scaleGizmo?.yGizmo.dragBehavior.onDragEndObservable.add((event: any) => {
            //  console.log("Position gizmo's y axis drag was ended", event);
            this.onMeshTransformUpdate("scale")
        })
        // this.gizmoManager.gizmos.positionGizmo.zGizmo.dragBehavior.onDragStartObservable.add((event: any) => {
        //     console.log("Position gizmo's z axis started to be dragged");
        // })
        this.gizmoManager.gizmos.scaleGizmo?.zGizmo.dragBehavior.onDragEndObservable.add((event: any) => {
            //    console.log("Position gizmo's z axis drag was ended", event);
            this.onMeshTransformUpdate("scale")
        })


        // this.gizmoManager.rotationGizmoEnabled = false;
        // this.gizmoManager.scaleGizmoEnabled = false;


    }
    /**
     * attach Gizmo control to mesh
     * @param {*} mesh
     * @memberof GizmoTool
     */
    attachOnMesh(mesh: any) {
        this.gizmoManager.positionGizmoEnabled = true;

        this.mesh = mesh;
        this.gizmoManager.attachToMesh(mesh);
        console.log(this.gizmoManager)
    }

    attachOnNode(Camera: any) {
        this.gizmoManager.attachToNode(Camera);
    }
    // m.attachToNode(camera2);

    /**
     * on gizmo update callback
     * @param {*} [options]
     * @memberof GizmoTool
     */
    onMeshTransformUpdate(options?: any) {
        console.log(this.mesh.tag);

        if (this.mesh != null) {

            console.log("2");
            if (DH_Constants.modePlane && DH_Constants.modePlane_editFromTop) {
                console.log("3");

                this.appManager.wallGeometry.updatePointWallFromTop(this.mesh);
            } else if (DH_Constants.modeCube && DH_Constants.modeCube_edit_Cube) {
                console.log("3");

                this.appManager.cubeGeometry.updateCube(this.mesh);
            }else if (DH_Constants.modeCube_editRoom && DH_Constants.modePlane_editFromTop && this.mesh.tag=="CornerBox") {
                console.log("3");

                this.appManager.wallGeometry.updatePointWallFromTop(this.mesh);
            }else if (DH_Constants.modeCube_editRoom && DH_Constants.modeCube_edit_Cube && this.mesh.tag=="cubeCornerBox") {
                console.log("3");

                this.appManager.cubeGeometry.updateCube(this.mesh);
            }

        }
    }

       /**
     * on gizmo update callback
     * @param {*} [options]
     * @memberof GizmoTool
     */
    onMeshDraging(event:any,axis:any) {
        // console.log("1");

        if (this.mesh != null) {

            // console.log("2");
            if (DH_Constants.modePlane && DH_Constants.modePlane_editFromTop) {
                // console.log("3");

              //  this.appManager.wallGeometry.updatePointWallFromTop(this.mesh);
            } else if (DH_Constants.modeCube && DH_Constants.modeCube_edit_Cube) {
                // console.log("3");

                this.appManager.cubeGeometry.ondragCubePoints(this.mesh,event,axis);
            }else if (DH_Constants.modeCube_editRoom && DH_Constants.modeCube_edit_Cube && this.mesh.tag=="cubeCornerBox") {
                console.log("3");

                this.appManager.cubeGeometry.ondragCubePoints(this.mesh,event,axis);
            }

        }
    }

    enablePositionGizmoOnly(xAxisScale: float, yAxisScale: float, zAxisScale: float) {
        // this.gizmoManager.gizmos.positionGizmo?.xGizmo.scaleRatio = 0.0;
        this.gizmoManager.positionGizmoEnabled = true;
        this.gizmoManager.rotationGizmoEnabled = false;
        this.gizmoManager.scaleGizmoEnabled = false;
        if (this.gizmoManager.gizmos.positionGizmo) {
            this.gizmoManager.gizmos.positionGizmo.xGizmo._scaleRatio = xAxisScale;
            this.gizmoManager.gizmos.positionGizmo.yGizmo._scaleRatio = yAxisScale;
            this.gizmoManager.gizmos.positionGizmo.zGizmo._scaleRatio = zAxisScale;
        }
        this.gizmoManager.positionGizmoEnabled = true;
        this.gizmoManager.rotationGizmoEnabled = false;
        this.gizmoManager.scaleGizmoEnabled = false;

    }

    getGizmoHandler(): GizmoHandler {
        return this;
    }
    updateCubeGizmo(var1: any, var2: any) {

        if (this.gizmoManager.gizmos && this.gizmoManager.gizmos.positionGizmo) {
            var xDif = (var1.x - var2.x);
            var yDif = (var1.y - var2.y);
            var zDif = (var1.z - var2.z);
            if (xDif < 0)
                xDif = -xDif;
            if (yDif < 0)
                yDif = -yDif;
            if (zDif < 0)
                zDif = -zDif;

            var greater = false;

            if (xDif >= yDif && xDif >= zDif) {
                //largest = "X";

                this.gizmoManager.gizmos.positionGizmo.xGizmo.scaleRatio = 1;
                this.gizmoManager.gizmos.positionGizmo.yGizmo.scaleRatio = 0.0;
                this.gizmoManager.gizmos.positionGizmo.zGizmo.scaleRatio = 0.0;
                if (var1.x > var2.x)
                    greater = true;
                // return greater;
            }
            else if (yDif >= xDif && yDif >= zDif) {
                //  largest = "Y";
                this.gizmoManager.gizmos.positionGizmo.xGizmo.scaleRatio = 0.0;
                this.gizmoManager.gizmos.positionGizmo.yGizmo.scaleRatio = 1.0;
                this.gizmoManager.gizmos.positionGizmo.zGizmo.scaleRatio = 0;
                if (var1.y > var2.y)
                    greater = true;
                // return greater;
            }
            else {
                // largest = "Z";
                this.gizmoManager.gizmos.positionGizmo.xGizmo.scaleRatio = 0.0;
                this.gizmoManager.gizmos.positionGizmo.yGizmo.scaleRatio = 0.0;
                this.gizmoManager.gizmos.positionGizmo.zGizmo.scaleRatio = 1;
                if (var1.z > var2.z)
                    greater = true;
                // return greater;
            }
            return greater;
        }
    }


}