import { Scene, ArcRotateCamera } from "@babylonjs/core";
import { AppManager } from "../ts/AppManager";
import { Handler } from "../ts/Handler";

export class EditRoom {
    private appManager: AppManager;
    private handler: Handler;
    private scene: Scene;
    private camera: ArcRotateCamera;

    private editRoomPosButton: any;
    private editRoomElementPosButton: any;
    private editRoomDoneButton: any;
    private buttons: any = [];
    // private cursor: any;

    constructor(appManager: AppManager) {
        this.appManager = appManager;
        this.scene = appManager.sceneManager.getScene();
        this.camera = this.appManager.sceneManager.getCamera();

        this.handler = this.appManager.handler.getHandler();

        this.editRoomPosButton = document.getElementById('EditRoomPosition');
        this.buttons.push(this.editRoomPosButton);
        this.editRoomElementPosButton = document.getElementById('EditRoomElementPosition');
        this.buttons.push(this.editRoomElementPosButton);

        this.editRoomDoneButton = document.getElementById('DoneRoomEdit');
        this.buttons.push(this.editRoomDoneButton);
        // this.cursor = this.appManager.DHCursor.getCursor();
    }
    private disableAllButton() {

        this.buttons.forEach((element: any) => {
            element.style.display = 'none';
        });

    }

    EditRoomPosition() {
        this.disableAllButton();
        if (this.editRoomElementPosButton != null)
            this.editRoomElementPosButton.style.display = 'block';
        if (this.editRoomDoneButton != null)
            this.editRoomDoneButton.style.display = 'block';
    }

    EditRoomElementPosition() {
        this.disableAllButton();
        if (this.editRoomPosButton != null)
            this.editRoomPosButton.style.display = 'block';
        if (this.editRoomDoneButton != null)
            this.editRoomDoneButton.style.display = 'block';
    }
    DoneRoomEdit(){
        this.disableAllButton();
    }


}