    constructor(appManager: AppManager) {
        this.appManager = appManager;
        this.scene = appManager.sceneManager.getScene();
        this.camera = this.appManager.sceneManager.getCamera();

        this.handler = this.appManager.handler.getHandler();
        this.cursor = this.appManager.DHCursor.getCursor();


        this.CreateWallButton = document.getElementById('CreateWall');
        this.CreateWallButtonDone = document.getElementById('CreateWallDone');
        this.CreateWallButtonMergeWithFirstPoint = document.getElementById('MergeWithFirstCorner');
        this.EditWallButton = document.getElementById('EditWall');
        this.EditWallDoneButton = document.getElementById('EditWallDone');
        this.MergeWallsButton = document.getElementById('MergeAllWalls');
        this.EditFromTopButton = document.getElementById('EditFromTop');
        this.EditFromTopDoneButton = document.getElementById('EditFromTopDone');
        this.FloorDoneButton = document.getElementById('FloorDone');
        this.transformMatrix = this.scene.getTransformMatrix()



    }

    private disableAllWallButton() {
        if (this.CreateWallButton != null)
            this.CreateWallButton.style.display = 'none';

        if (this.CreateWallButtonDone != null)
            this.CreateWallButtonDone.style.display = 'none';

        if (this.CreateWallButtonMergeWithFirstPoint != null)
            this.CreateWallButtonMergeWithFirstPoint.style.display = 'none';

        if (this.EditWallButton != null)
            this.EditWallButton.style.display = 'none';

        if (this.EditWallDoneButton != null)
            this.EditWallDoneButton.style.display = 'none';

        if (this.MergeWallsButton != null)
            this.MergeWallsButton.style.display = 'none';

        if (this.EditFromTopButton != null)
            this.EditFromTopButton.style.display = 'none';
        if (this.EditFromTopDoneButton != null)
            this.EditFromTopDoneButton.style.display = 'none';
        if (this.FloorDoneButton != null)
            this.FloorDoneButton.style.display = 'none';


    }

    /**
     * Reset to default condition of wall mode
     * where user needs to select options of create edit and merge
     */
    enableWallOptionsUi() {

        this.disableAllWallButton();

        if (DH_Constants.modePlane) {
            var wallKeys = Object.keys(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup);

            if (this.CreateWallButton != null && wallKeys.length == 0)
                this.CreateWallButton.style.display = 'block';
            if (this.EditWallButton != null && wallKeys.length > 0)
                this.EditWallButton.style.display = 'block';
        }

        DH_Constants.modePlane_createCorners = false;
        DH_Constants.modePlane_editWallUV = false;
        DH_Constants.modePlane_editFromTop = false;
    }

    disableAll() {
        console.log("disableAll");
        var wallGroupList: any = Object.keys(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup);
        for (let index = 0; index < wallGroupList.length; index++) {
            const GroupName = wallGroupList[index];
        console.log("disableAll ",GroupName);

            // console.log(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[GroupName].cubeRoot);
            this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[GroupName].WallsNode.setEnabled(false);
            this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[GroupName].CornersNode.setEnabled(false);
        }
    }
    enableAll() {
        console.log("enableAll");

        var wallGroupList: any = Object.keys(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup);
        for (let index = 0; index < wallGroupList.length; index++) {
            const GroupName = wallGroupList[index];
        console.log("enableAll ",GroupName);

            // console.log(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[GroupName].cubeRoot);
            this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[GroupName].WallsNode.setEnabled(true);
            this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[GroupName].CornersNode.setEnabled(true);
        }
    }
