        // lin1.actionManager = new ActionManager(this.scene);
        // lin1.actionManager.registerAction(new ExecuteCodeAction(ActionManager.OnPickDownTrigger, () => {
        //     console.log(lin1)
        //     // this.appManager.gizmoHandler.attachOnMesh(lin1);
        //     lin1.addBehavior(pointerDragBehavior);
        // }));

        // lin2.actionManager = new ActionManager(this.scene);
        // lin2.actionManager.registerAction(new ExecuteCodeAction(ActionManager.OnPickDownTrigger, () => {
        //     console.log(lin2)

        // //    this.appManager.gizmoHandler.attachOnMesh(lin2);

        // }));


        // var pointerDragBehavior = new PointerDragBehavior();

        // // Use drag plane in world space
