    getCenterPoint(_box2: any, _box1: any, boxName: string, tagName: string) {
        var direction = this.minus(_box2, _box1);
        var tempvct: Vector3 = new Vector3(0, 0, 0);
        tempvct.x = _box1.x + direction.x / 2;
        tempvct.y = _box1.y + direction.y / 2;
        tempvct.z = _box1.z + direction.z / 2;
        var centerBox: any = this.mousePointer3dBottom.clone(boxName);
        centerBox.name = boxName;
        centerBox.tag = tagName;
        centerBox.position.set(tempvct.x, tempvct.y, tempvct.z);
        centerBox.renderingGroupId = 3;
        centerBox.isVisible = false;
        return [centerBox, tempvct];
    }

