    AdjustWallFromTop() {
        this.disableAllWallButton();
        this.appManager.cameraManager.setVerticalCamera();
        this.appManager.sphereManager.toggleSphere(false);
        DH_Constants.updatefov = false;
        DH_Constants.modePlane_editFromTop = true;

        var wallKeys = Object.keys(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData)
        if (!this.grid)
            this.grid = this.handler.createGrid(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[0]].corners[0].position.y);
        if (this.grid)
            this.grid.isVisible = true;
        for (let index = 0; index < wallKeys.length; index++) {
            // const wallname = wallKeys[index];
            this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[0].isVisible = true;

        }

        if (this.EditFromTopDoneButton != null)
            this.EditFromTopDoneButton.style.display = 'block';

    }
    updatePointWallFromTop(mesh: any) {
        var wallKeys = Object.keys(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData)
        console.log(mesh);
        // var SelectedWall;
        // var ConnectedWall;
        for (let index = 0; index < wallKeys.length; index++) {
            console.log("4");

            if (mesh.wallName === wallKeys[index]) {
                console.log("5");

                //updating Selected mesh Position
                this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[1].position.x =
                    this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[0].position.x;
                this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[1].position.z =
                    this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[0].position.z
                if (index === 0) {
                    console.log("F UPDATE WALL", index, "  ", wallKeys.length - 1)
                    this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[wallKeys.length - 1]].corners[2].position =
                        this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[1].position;
                    this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[wallKeys.length - 1]].corners[3].position =
                        this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[0].position
                }
                else {
                    console.log("O UPDATE WALL", index, "  ", index - 1, "  ", wallKeys[index - 1])
                    this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index - 1]].corners[2].position =
                        this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[1].position;
                    this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index - 1]].corners[3].position =
                        this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[0].position
                }


                this.updateWall(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[0],
                     false, "wall");
                if (index == 0)
                    this.updateWall(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[wallKeys.length - 1]].corners[0], 
                        false, "wall");
                else
                    this.updateWall(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index - 1]].corners[0], 
                        false, "wall");

                break;
            }

        }
    }

