    getPosition2(vertices: any, twod: any) {
        var refVertices = [...vertices];
        var verticeslocal = new Float32Array(vertices.length * 3);

        var verticeslocaltwod: any = new Float32Array(vertices.length * 2);

        for (let i = 1; i <= vertices.length; i++) {
            verticeslocal[i * 3 - 3] = refVertices[i - 1].x;
            verticeslocal[i * 3 - 2] = refVertices[i - 1].y;
            verticeslocal[i * 3 - 1] = refVertices[i - 1].z;

            if (twod) {
                verticeslocaltwod[i * 3 - 3] = refVertices[i - 1].x;
                verticeslocaltwod[i * 3 - 2] = refVertices[i - 1].z;
            }

            // var x,y,z;
            // x=parseFloat(refVertices[i - 1].x).toFixed(2);
            // y=parseFloat(refVertices[i - 1].y).toFixed(2);
            // z=parseFloat(refVertices[i - 1].z).toFixed(2);
            // // console.log(x,y,z);
            // verticeslocal[i * 3 - 3] = x;
            // verticeslocal[i * 3 - 2] = y;
            // verticeslocal[i * 3 - 1] =z;
        }
        // console.log(verticeslocal);

        var pp = [];
        for (let i = 1; i <= verticeslocaltwod.length; i++) {
            pp.push(parseFloat(parseFloat(verticeslocaltwod[i]).toFixed(2)));
        }
        // console.log(pp);
        if (!twod)
            return verticeslocal;
        else
            return [verticeslocal, pp]
    }


    enableEditRoom() {
        this.disableAllWallButton();
        this.appManager.cameraManager.setVerticalCamera();
        this.appManager.sphereManager.toggleSphere(false);
        DH_Constants.updatefov = false;
        DH_Constants.modePlane_editFromTop = true;

        var wallKeys = Object.keys(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData)
        if (!this.grid)
            this.grid = this.handler.createGrid(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[0]].corners[0].position.y);
        if (this.grid)
            this.grid.isVisible = true;
        for (let index = 0; index < wallKeys.length; index++) {
            // const wallname = wallKeys[index];
            this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].wallGroup[DH_Constants.currentWallGroupName].WallsData[wallKeys[index]].corners[0].isVisible = true;

        }

    }

    calculateHeight(top: any, down: any) {
        DH_Constants.wallHeight = Vector3.Distance(top, down);
        DH_Constants.bottomY = down.y;
        DH_Constants.topY = top.y;

    }



