import { Color3, MeshBuilder, Scene, StandardMaterial, Vector3 } from "@babylonjs/core";
import { DH_Constants } from "../Room/DH_Constants";
import { AppManager } from "./AppManager";
// import { GridMaterial } from "babylonjs-materials";
import { Mesh } from "babylonjs";
// import { GridMaterial } from "babylonjs-materials/legacy/legacy-grid";
import { GridMaterial } from "@babylonjs/materials/grid";


export class Handler {
    private appManager: AppManager;
    private scene: any;
    mosusePointer: any;

    constructor(appManager: AppManager) {
        this.appManager = appManager;
        this.scene = this.appManager.sceneManager.getScene();
    }

    enableMousePointer(active: boolean) {
        if (this.mosusePointer == undefined) {
            var myMaterial2 = new StandardMaterial("myMaterialgreen", this.scene);
            myMaterial2.diffuseColor = new Color3(0, 1, 0.25);

            var myMaterial360 = new StandardMaterial("myMaterial360", this.scene);
            myMaterial360.diffuseColor = new Color3(0, 1, 1);
            this.mosusePointer = MeshBuilder.CreateBox("refmousePointer", { size: 80 }, this.scene);
            var d_RefLPo = MeshBuilder.CreateBox("refmousePointerLine", { width: 10, height: 5000 }, this.scene);
            d_RefLPo.isPickable = false;
            d_RefLPo.parent = this.mosusePointer;
            this.mosusePointer.material = myMaterial2;
            d_RefLPo.material = myMaterial360;
            this.mosusePointer.renderingGroupId = 1;
            this.mosusePointer.isPickable = false;
        }

        this.mosusePointer.isVisible(active);

    }
    public createBox(pos: Vector3, name: any, options?: any) {
        console.log(options)
        var _size = DH_Constants.size_ControlBox;
        if (options && options.hasOwnProperty("size"))
            _size = options.size;
        var box = MeshBuilder.CreateBox(name, { size: _size }, this.scene);
        box.position.set(pos.x, pos.y, pos.z)
        return box;
    }


    public createLine(linePoints: any, name: any) {

        var line = MeshBuilder.CreateLines(name, { points: linePoints }, this.scene);
        return line;
    }
    public createLineTube(linePoints: any, name: any, radius: any) {

        var line = MeshBuilder.CreateTube("tube", { path: linePoints, radius: radius }, this.scene);
        return line;
    }
    public getHandler(): Handler {
        return this;
    }

    public createGrid(yPosition: any) {

        var gridMaterial = new GridMaterial("grid", this.scene);
        gridMaterial.majorUnitFrequency = 10;
        gridMaterial.minorUnitVisibility = 0.3;
        gridMaterial.gridRatio = 10;
        gridMaterial.backFaceCulling = false;
        gridMaterial.mainColor = new Color3(1, 1, 1);
        gridMaterial.lineColor = new Color3(1, 1, 1);
        gridMaterial.opacity = 0.8;
        gridMaterial.zOffset = 1;

        const grid: any = Mesh.CreateGround("grid", 100000, 100000, 2, this.scene);
        grid.position.set(0, 0, 0);
        grid.material = gridMaterial;
        grid.isPickable = false;
        grid.renderingGroupId = 1;
        grid.position.y = yPosition;

        return grid;
    }

    public getPoints(_box2: any, _box1: any) {
        var vct_ = this.minus(_box2, _box1);
        // console.log(vct_)
        var arr_vct = [];
        for (var i = 1; i < DH_Constants.divPoint; i++) {
            var tempvct = new Vector3(0, 0, 0);
            tempvct.x = _box1.x + ((vct_.x * i) / DH_Constants.divPoint)
            tempvct.y = _box1.y + ((vct_.y * i) / DH_Constants.divPoint)
            tempvct.z = _box1.z + ((vct_.z * i) / DH_Constants.divPoint)
            arr_vct.push(tempvct);
        }
        return arr_vct;
    }
    public minus(last: any, mesh: any) {

        var direction = new Vector3(last.x - mesh.x, last.y - mesh.y, last.z - mesh.z);
        return direction;
    }
    public getUvPoint(point: any) {
        var pickinfo: any = this.scene.pick(point.x, point.y, (mesh: any) => {
            return mesh === this.appManager.sphereManager.getSphere();
        });
        // console.log(point)
        // console.log(pickinfo.getTextureCoordinates())
        return pickinfo.getTextureCoordinates();
    }
    public getPosition(vertices: any, twod?: any) {
        var refVertices = [...vertices];
        var verticeslocal = new Float32Array(vertices.length * 3);

        var verticeslocaltwod: any = new Float32Array(vertices.length * 2);

        for (let i = 1; i <= vertices.length; i++) {
            verticeslocal[i * 3 - 3] = refVertices[i - 1].x;
            verticeslocal[i * 3 - 2] = refVertices[i - 1].y;
            verticeslocal[i * 3 - 1] = refVertices[i - 1].z;

            if (twod) {
                verticeslocaltwod[i * 3 - 3] = refVertices[i - 1].x;
                verticeslocaltwod[i * 3 - 2] = refVertices[i - 1].z;
                console.log(refVertices[i - 1])
            }

            // var x,y,z;
            // x=parseFloat(refVertices[i - 1].x).toFixed(2);
            // y=parseFloat(refVertices[i - 1].y).toFixed(2);
            // z=parseFloat(refVertices[i - 1].z).toFixed(2);
            // // console.log(x,y,z);
            // verticeslocal[i * 3 - 3] = x;
            // verticeslocal[i * 3 - 2] = y;
            // verticeslocal[i * 3 - 1] =z;
        }
        // console.log(verticeslocal);

        var pp: any = [];
        if (twod) {
            for (let i = 0; i < verticeslocaltwod.length; i++) {
                // if (verticeslocaltwod[i] != NaN)
                var val: any = parseFloat(verticeslocaltwod[i])
                val = parseFloat(val.toFixed(2));
                pp.push(val);
                // else pp.push(verticeslocaltwod[i - 1])
            }
        }
        // console.log(verticeslocaltwod);
        // console.log(pp);
        if (!twod)
            return verticeslocal;
        else
            return [verticeslocal, pp]
    }
}