    {
        console.log("* GET LOCAL SETTING *");
        let names = ['Room.json'];
        let requests = names.map(name => fetch(`${'assets'}/${name}`));

        Promise.all(requests)
        .then(resp => Promise.all(resp.map(r => r.json()) ))
        .then(result => {
            console.log("Json DONE");
            console.log(result[0]);

            this.appManager.roomManager.initRoomData(result[0])
           
        });
    }
