    updateCube(selectedBox: any) {
        console.log("updateCube")
        this.updateCubePlane(0, selectedBox.cubeName, this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[0], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[2], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[3], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[1], false);
        this.updateCubePlane(1, selectedBox.cubeName, this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[1], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[3], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[7], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[5], false);
        this.updateCubePlane(2, selectedBox.cubeName, this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[5], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[7], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[6], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[4], false);
        this.updateCubePlane(3, selectedBox.cubeName, this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[4], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[6], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[2], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[0], false);
        this.updateCubePlane(4, selectedBox.cubeName, this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[3], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[7], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[6], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[2], true);
        this.updateCubePlane(5, selectedBox.cubeName, this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[1], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[5], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[4], this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[selectedBox.cubeName].cubeCornerPoints[0], true);

    }

    private updateCubePlane(Index: any, cubeName: any, p0: any, p1: any, p2: any, p3: any, normals: any) {
        try {

            var vertex = [];
            vertex.push(p0);
            vertex.push(p1);
            var topPoints = this.handler.getPoints(p2, p1);
            var bottomPoints = this.handler.getPoints(p3, p0);
            for (var i = 0; i < DH_Constants.divPoint - 1; i++) {
                vertex.push(topPoints[i])
                vertex.push(bottomPoints[i])
            }
            vertex.push(p2);
            vertex.push(p3);
            var _uvs: any = [];
            // var DH_Constants.modeCube_edit_CubeWallUVPosition = true;
            if (DH_Constants.modeCube_edit_CubeWallUVPosition) {
                vertex.forEach(element => {
                    var coordinates = Vector3.Project(
                        element,
                        Matrix.Identity(),
                        this.scene.getTransformMatrix(),
                        this.camera.viewport.toGlobal(
                            this.appManager.sceneManager.getEngine().getRenderWidth(),
                            this.appManager.sceneManager.getEngine().getRenderHeight(),
                        ));

                    var uvCoordinates = this.handler.getUvPoint(coordinates);
                    _uvs.push(uvCoordinates.x);
                    _uvs.push(uvCoordinates.y);

                });
            }

            var newPlanepositions = this.handler.getPosition(vertex);

            //for 10
            var flag = true;
            var index = 0;
            var indicesSeq = [];
            for (var i = 0; i < DH_Constants.divPoint; i++) {
                if (flag) {
                    indicesSeq.push(index++);
                    indicesSeq.push(index++);
                    indicesSeq.push(++index);
                    indicesSeq.push(index - 2);
                    indicesSeq.push(index - 1);
                    indicesSeq.push(index);
                    flag = !flag;
                } else {
                    indicesSeq.push(index--);
                    indicesSeq.push(index);
                    index += 3;
                    indicesSeq.push(index);
                    indicesSeq.push(index - 3);
                    indicesSeq.push(index - 1);
                    indicesSeq.push(index);
                }
            }
            var newPlanenormals: any = [];
            var newPlanevertexData = new BABYLON.VertexData();
            BABYLON.VertexData.ComputeNormals(newPlanepositions, indicesSeq, newPlanenormals);
            // BABYLON.VertexData._ComputeSides(BABYLON.Mesh.FRONTSIDE, newPlanepositions, indicesSeq, newPlanenormals, _uvs);
            //Assign positions, indices and normals to vertexData
            // console.log(newPlanepositions)
            newPlanevertexData.positions = newPlanepositions;
            newPlanevertexData.indices = indicesSeq;
            if (!normals)
                newPlanevertexData.normals = newPlanenormals;
            if (DH_Constants.modeCube_edit_CubeWallUVPosition)
                newPlanevertexData.uvs = _uvs;
            newPlanevertexData.applyToMesh(this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[cubeName].cube[Index].plane);
            this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[cubeName].cube[Index].vertexData = newPlanevertexData;
            // updating = false;


            // let newPlaneMin = this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[cubeName].cube[Index].plane.getBoundingInfo().boundingBox.minimum;
            // let newPlaneMax = this.appManager.roomManager.RoomData[DH_Constants.currentRoomName].cubeGroup[cubeName].cube[Index].plane.getBoundingInfo().boundingBox.maximum;
            // if (Index != 0) {
            //     newMin = Vector3.Minimize(newMin, newPlaneMin);
            //     newMax = Vector3.Maximize(newMax, newPlaneMax);
            // }
            // else {
            //     newMin = newPlaneMin;
            //     newMax = newPlaneMax;
            // }
        } catch (error) {
            console.log(error);
        }
    }
