import React, { Component } from "react";

import './main.css'
import { AppManager } from "./ts/AppManager";

declare var VP: any;
declare var window: any;

export default class App extends Component {


  appInit: AppManager;
  constructor(props: any) {
    super(props)



    this.state = {

    };

  };


  componentDidMount = () => {
    setTimeout(() => {

      this.appInit = new AppManager();
      this.panelSet();
    }, 2000);
  };




  render() {
    return <div id="scene-container">
      <canvas id="renderCanvas"></canvas>
      <div id="cursor" className="cursorBox">
        Select Options From Side Menu
      </div>
      <button id="CreateWall" className="LeftBottom40Button" onClick={this.planeModeCreateWall}>
        Create Walls
      </button>
      <button id="CreateWallDone" className="LeftBottom40Button" onClick={this.planeModeCreateWallDone}>
        Done
      </button>

      <button id="MergeWithFirstCorner" className="RightBottom40Button" onClick={this.planeModeMergeWithFirstCorner}>
        Merge With First Corner
      </button>


      <button id="EditWall" className="RightBottom40Button" onClick={this.planeModeEditWall}>
        Edit Wall
      </button>

      <button id="EditWallDone" className="BottomCenterButtom" onClick={this.planeModeEditWallDone}>
        Edit Wall Done
      </button>

      <button id="MergeAllWalls" className="BottomCenterButtom" onClick={this.planeModeMergeAllWalls}>
        Merge All Walls
      </button>

      <button id="EditFromTop" className="BottomCenterButtom" onClick={this.planeModeAdjustWallsFromTop}>
        Adjust Walls
      </button>

      <button id="EditFromTopDone" className="BottomCenterButtom" onClick={this.planeModeAdjustWallsFromTopDone}>
        Adjust Walls done
      </button>


      <button id="FloorDone" className="BottomCenterButtom" onClick={this.planeModeFloorDone}>
        Floor done
      </button>

      <button id="NewCube" className="LeftBottom40Button" onClick={this.cubeModeCreateNewCube}>
        New Cube
      </button>
      <button id="EditCube" className="RightBottom40Button" onClick={this.cubeModeEditCube}>
        Edit Cube
      </button>


      <div id="EditCubeToggleParent" className="BottomCenterButtomBgBottom15">
        Texture Update
        <label className="switch">
          <input type="checkbox" id="CubeEditToggle" onClick={this.cubeModeEditCubeToggle} />
          <span className="slider"></span>
        </label>
      </div>

      <button id="EditRoomPosition" className="LeftBottom40Button" onClick={this.editRoomModePosition}>
        Adjust Room Position
      </button>

      <button id="EditRoomElementPosition" className="LeftBottom40Button" onClick={this.editRoomModeElementPosition}>
        Adjust Room Element Position
      </button>
      <button id="DoneRoomEdit" className="RightBottom40Button" onClick={this.editRoomModeDone}>
        Done
      </button>

      <div id="mySidenav" className="sidenav">
        <a href="javascript:void(0)" className="closebtn"

        >&times;</a>
        {/* <!-- <a href="#">About</a> --> */}
        <button className="accordion">Change Rooms </button>
        <div className="accordionpanel" id="accordionpanelChangeRoom">
        </div>
        <button className="accordion2">Enable Rooms </button>
        <div className="accordionpanel" id="accordionpanelEnableRoom">
        </div>

        <button className="accordion3">Mode </button>
        <div className="accordionpanel" id="accordionpanelModes">
          <button onClick={this.planeMode} className="accordionbutton" id="Wall Create">Wall</button>
          <button onClick={this.cubeMode} className="accordionbutton" id="Wall Create">Cube</button>
          <button onClick={this.editRoomMode} className="accordionbutton" id="Edit Room">Edit Room</button>
        </div>

        <button className="accordion4">Camera</button>
        <div className="accordionpanel" id="accordionpanelCamera">
          <button onClick={this.DefaultCamera} className="accordionbutton" id="Default Camera">Default Camera</button>
          <button onClick={this.FreeCamera} className="accordionbutton" id="Free Camera">Free Camera</button>
          <button onClick={this.TopCamera} className="accordionbutton" id="Top Camera">Top Camera</button>
        </div>
      </div>
      <span style={{
        fontSize: '30px',
        cursor: 'pointer',
        position: 'absolute'
      }}
        onClick={this.openNav}
      >&#9776;</span>

      <div id="myModal" className="modal">

        <div className="modal-content">
          <span className="close">&times;</span>

          <p>Some text in the Modal..</p>
          <p>Some text in the Modal..</p>
          <button type="button" className="modalButton">OK</button>
        </div>

      </div>



    </div>

  }
  DefaultCamera() {
    window.wDefaultCamera();
  }

  FreeCamera() {
    window.wFreeCamera();
  }

  TopCamera() {
    window.wTopCamera();
  }

  openNav() {
    var dd: any = document.getElementById("mySidenav");
    dd.style.width = "250px";
  }
  closeNav() {
    var dd: any = document.getElementById("mySidenav");
    dd.style.width = "0px";
  }

  panelSet() {
    var acc: any = document.getElementsByClassName("accordion");
    acc[0].classList.toggle("active");

    acc[0].addEventListener("click", () => {
      var panel: any = acc[0].nextElementSibling;
      if (panel.style.display === "block") {
        panel.style.display = "none";
      } else {
        panel.style.display = "block";
      }
    });


    var acc2: any = document.getElementsByClassName("accordion2");

    acc2[0].addEventListener("click", function () {
      acc2[0].classList.toggle("active");
      var panel: any = acc2[0].nextElementSibling;
      if (panel.style.display === "block") {
        panel.style.display = "none";
      } else {
        panel.style.display = "block";
      }
    });


    var acc3: any = document.getElementsByClassName("accordion3");
    acc3[0].addEventListener("click", function () {
      console.log(acc3[0])
      acc3[0].classList.toggle("active");
      var panel: any = acc3[0].nextElementSibling;
      if (panel.style.display === "block") {
        panel.style.display = "none";
      } else {
        panel.style.display = "block";
      }
    });

    var acc4: any = document.getElementsByClassName("accordion4");
    acc4[0].addEventListener("click", function () {
      console.log(acc4[0])
      acc4[0].classList.toggle("active");
      const panel: any = acc4[0].nextElementSibling;
      if (panel.style.display === "block") {
        panel.style.display = "none";
      } else {
        panel.style.display = "block";
      }
    });


  }

  planeMode() {
    // console.log(this.appInit)
    var dd: any = document.getElementById("mySidenav");
    dd.style.width = "0px";
    window.wplanMode();

    // this.appInit.modeManger.toggleWallMode();
  }

  planeModeCreateWall() {
    window.wplaneModeCreateWall();

  }
  planeModeCreateWallDone() {
    window.wplaneModeCreateWallDone();

  }

  planeModeMergeWithFirstCorner() {
    window.wplaneModeMergeWithFirstCorner();

  }

  planeModeEditWall() {
    window.wplaneModeEditWall();
  }

  planeModeEditWallDone() {
    window.wplaneModeEditWallDone();
  }

  planeModeMergeAllWalls() {
    window.wplaneModeMergeAllWalls();
  }
  planeModeAdjustWallsFromTop() {
    window.wplaneModeAdjustWallsFromTop();
  }
  planeModeAdjustWallsFromTopDone() {
    window.wplaneModeAdjustWallsFromTopDone();
  }

  planeModeFloorDone() {
    window.wplaneModeFloorDone();
  }


  cubeMode() {
    var dd: any = document.getElementById("mySidenav");
    dd.style.width = "0px";
    window.wcubeMode();
  }
  cubeModeCreateNewCube() {

    window.wcubeModeCreateNewCube();
  }
  cubeModeEditCube() {

    window.wcubeModeEditCube();
  }
  cubeModeSelectCube() {

    window.wcubeModeEditCube();
  }
  cubeModeEditCubeToggle() {

    var checkBox: any = document.getElementById("CubeEditToggle");
    console.log(checkBox.checked)
    window.wcubeModeEditCubeUV(checkBox.checked);
  }

  editRoomMode() {
    window.wtoggleEditRoomMode();
  }

  editRoomModePosition() {
    window.wEditRoomPosition();
  }
  editRoomModeElementPosition() {
    window.weditRoomModeElementPosition();
  }

  editRoomModeDone() {
    window.wEditRoomDone();
  }

}
